.user__container {
  display: flex;
  flex-direction: row;
  margin: 20px 50px;
}
.user__img {
  width: 120px;
  align-self: center;
  margin-top: 10px;
}
.user__img img {
  width: 80px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.follow__button button {
  font-size: 20px;
}
.user__description {
  width: 50%;
  padding: 10px;
  height: 100px;
  border: 1px solid gray;
  font-size: 20px;
  align-self: center;
  display: flex;
  justify-content: space-around;
  border-radius: 10px;
  flex-wrap: wrap;
  margin-left: 10px;
}
.item {
  margin-right: auto;
  width: 200px;
  overflow: hidden;
}
.item__right {
  text-align: right;
  margin-right: 20px;
}
.item__right:last-child {
  margin-top: -20px;
}

.item_quote {
  font-size: 14px;
}
